<template>
  <div class="list row">
    <div class="col-md-12">
      <center>
        <b-row align-v="center" align-h="center">
          <b-col cols="3"> <b-img src="giftcard.png" /></b-col>
          <b-col cols="6">
            <div class="float-left"><h2>Manage Gift Cards</h2></div>
          </b-col>
        </b-row>

        <div>
          <b-table
            small
            :fields="fields"
            :items="cardList"
            responsive="sm"
            class="ml-1 mt-3"
          >
            <!-- A virtual column -->
            <template #cell(index)="data">
              {{ data.index + 1 }}
            </template>

            <!-- A custom formatted column -->
            <template #cell(cardNo)="data">
              <b-img
                :src="getCardImg(data.value)"
                rounded="circle"
                width="32"
                height="32"
              />
              <b class="text-info">{{ data.value }}</b>
            </template>

            <template #cell(balance)="data">
              ${{ data.value.toFixed(2) }}
            </template>

            <template #cell(pinEnabled)="data">
              <div v-if="data.item.pin.length > 0">
                <b-img
                  src="checkmark.png"
                  rounded="circle"
                  width="32"
                  height="32"
                />
              </div>
              <div v-else>
                <b-img
                  src="crossmark.png"
                  rounded="circle"
                  width="32"
                  height="32"
                />
              </div>
            </template>

            <template #cell(Action)="data">
              <b-row>
                <b-col cols="6" class="nopadding">
                  <b-button
                    block
                    size="sm"
                    class="badge-default mr-1"
                    variant="outline-primary"
                    @click.prevent="showChangePINModal(data.item)"
                  >
                    <div v-if="data.item.pin.length == 0">Set PIN</div>
                    <div v-else>Change PIN</div></b-button
                  ></b-col
                >
                <b-col cols="6">
                  <b-button
                    block
                    size="sm"
                    class="badge-default mr-1"
                    variant="outline-primary"
                    @click.prevent="showTransferModal(data.item)"
                    >Transfer</b-button
                  >
                </b-col>
              </b-row>
            </template>

            <!-- Optional default data cell scoped slot -->
            <template #cell()="data">
              <i>{{ data.value }}</i>
            </template>
          </b-table>
        </div>
      </center>
    </div>

    <b-modal
      ref="my-modal-changePin"
      :title="'Change PIN for ' + currentCard.cardNo"
      size="sm"
      centered
      hide-footer
      no-close-on-esc
      no-close-on-backdrop
      hide-header-close
    >
      <div v-if="currentCard.pin && currentCard.pin.length > 0">
        <b-row>
          <b-col cols="6" align-v="center">
            <label for="oldPin"
              ><div class="text-info">Current PIN:</div></label
            >
          </b-col>
          <b-col>
            <b-form-input
              size="sm"
              v-model="oldPin"
              type="password"
              @keyup="clearError()"
              @keyup.enter="changePin()"
            ></b-form-input>
          </b-col>
        </b-row>
      </div>
      <b-row class="mt-1" align-v="center">
        <b-col cols="6">
          <label for="newPin"><div class="text-info">New PIN:</div></label>
        </b-col>
        <b-col>
          <b-form-input
            size="sm"
            v-model="newPin"
            type="password"
            @keyup="clearError()"
            @keyup.enter="changePin()"
          ></b-form-input>
        </b-col>
      </b-row>
      <b-row class="mt-1" align-v="center">
        <b-col cols="6">
          <label for="newPin1"
            ><div class="text-info">Confirmed PIN:</div></label
          >
        </b-col>
        <b-col>
          <b-form-input
            size="sm"
            v-model="newPin1"
            type="password"
            @keyup="clearError()"
            @keyup.enter="changePin()"
          ></b-form-input>
        </b-col>
      </b-row>
      <div v-if="errorMsg != ''">
        <b-alert class="mt-1" show variant="warning">{{ errorMsg }}</b-alert>
      </div>
      <b-row class="mt-2">
        <b-col cols="6">
          <b-button variant="outline-success" block @click="changePin()"
            >OK</b-button
          >
        </b-col>
        <b-col>
          <b-button
            variant="outline-success"
            block
            @click="$refs['my-modal-changePin'].hide()"
            >Cancel</b-button
          >
        </b-col>
      </b-row>
    </b-modal>

    <b-modal
      ref="my-modal-transfer"
      title="Transfer Balance"
      size="md"
      centered
      hide-footer
      no-close-on-esc
      no-close-on-backdrop
      hide-header-close
    >
      <b-row class="mt-1" align-v="center" align-h="start">
        <b-col cols="4">
          <label for="fromCard"><div class="text-info">From Card:</div></label>
        </b-col>
        <b-col>
          <b-img
            :src="getCardImg(currentCard.cardNo)"
            rounded="circle"
            width="32"
            height="32"
          />
          {{ currentCard.cardNo }} (${{ currentCard.balance.toFixed(2) }})
        </b-col>
      </b-row>

      <b-row class="mt-1" align-v="center">
        <b-col cols="4">
          <label for="toCard"><div class="text-info">To Card:</div></label>
        </b-col>
        <b-col>
          <b-form-select v-model="toCardNo" @change="clearError()">
            <b-form-select-option value=""
              >- Please select a card -</b-form-select-option
            >
            <b-form-select-option
              :value="card.cardNo"
              :disabled="card.cardNo == currentCard.cardNo"
              v-for="(card, i) in cardList"
              :key="i"
            >
              {{ card.cardNo }} (${{ card.balance.toFixed(2) }})
            </b-form-select-option>
          </b-form-select>
        </b-col>
      </b-row>

      <b-row class="mt-1" align-v="center">
        <b-col cols="4">
          <label for="amount"><div class="text-info">Amount:</div></label>
        </b-col>
        <b-col cols="6">
          <b-input-group prepend="$">
            <b-form-input
              id="amount"
              type="number" min="0"
              v-model="transferAmt"
              @blur="transferAmt = Math.abs(Number(transferAmt)).toFixed(2)"
            ></b-form-input>
          </b-input-group>
        </b-col>
        <b-col cols="2">
          <b-form-checkbox
            v-model="allAmt"
            name="check-button"
            @change="toggleAll()"
          >
            <b>ALL</b>
          </b-form-checkbox>
        </b-col>
      </b-row>

      <hr />
      <div v-if="currentCard.pin && currentCard.pin.length > 0">
        <b-row>
          <b-col cols="4" align-v="center">
            <label for="oldPin"
              ><div class="text-info">PIN:</div></label
            >
          </b-col>
          <b-col>
            <b-form-input
              size="sm"
              v-model="oldPin"
              type="password"
              @keyup="clearError()"
              @keyup.enter="transfer()"
            ></b-form-input>
          </b-col>
        </b-row>
      </div>

      <div v-if="errorMsg != ''">
        <b-alert class="mt-1" show variant="warning">{{ errorMsg }}</b-alert>
      </div>
      <b-row class="mt-2">
        <b-col cols="6">
          <b-button variant="outline-success" block @click="transfer()"
            >OK</b-button
          >
        </b-col>
        <b-col>
          <b-button
            variant="outline-success"
            block
            @click="$refs['my-modal-transfer'].hide()"
            >Cancel</b-button
          >
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>

<script>
import barcodeService from "../services/BarCodeService";

export default {
  name: "giftcardList",
  data() {
    return {
      fields: [
        // A virtual column that doesn't exist in items
        "Index",
        // A column that needs custom formatting
        { key: "cardNo", label: "Card No", sortable: true },
        {
          key: "balance",
          label: "Balance",
          sortable: true,
          class: "text-right",
        },
        { key: "pinEnabled", label: "PIN Enabled", class: "text-center" },
        "Action",
      ],
      cardList: [],
      currentCard: { cardNo: "", balance: 0 },
      oldPin: "",
      newPin: "",
      newPin1: "",
      errorMsg: "",
      toCardNo: "",
      transferAmt: 0,
      allAmt: false,
    };
  },
  methods: {
    getAllCards() {
      barcodeService.getAllGiftCards().then((resp) => {
        let result = resp.data;
        console.log(result);

        this.cardList = result;
      });
    },

    showChangePINModal(card) {
      this.currentCard = card;
      this.oldPin = "";
      this.newPin = "";
      this.newPin1 = "";
      this.clearError();
      this.$refs["my-modal-changePin"].show();
    },

    showTransferModal(card) {
      this.currentCard = card;
      this.toCardNo = "";
      this.oldPin = "";
      this.transferAmt = "";
      this.allAmt = false;
      this.clearError();
      this.$refs["my-modal-transfer"].show();
    },

    getCardImg(cardNo) {
      if (cardNo != null) {
        let imgName = cardNo.substring(0, cardNo.length - 3);
        return imgName.toLowerCase() + ".png";
      }
      return "";
    },

    toggleAll() {
      this.transferAmt = this.allAmt ? this.currentCard.balance.toFixed(2) : "";
    },

    clearError() {
      this.errorMsg = "";
    },

    changePin() {
      // check input
      if (this.currentCard.pin.length > 0 && this.oldPin == "") {
        this.errorMsg = "Please enter your old PIN";
        return;
      }

      /* allow set new pin to blank 
      if (this.newPin == "" || this.newPin1 == "") {
        this.errorMsg = "Please enter your new PIN";
        return;
      }*/

      if (this.currentCard.pin.length > 0 && this.oldPin == this.newPin1) {
        this.errorMsg = "Old and New PIN can't be the same";
        return;
      }

      if (this.newPin != this.newPin1) {
        this.errorMsg = "PIN doesn't match";
        return;
      }

      barcodeService
        .changePIN(this.currentCard.cardNo, this.oldPin, this.newPin)
        .then((resp) => {
          let result = resp.data;
          console.log(result);

          let status = result.split(":")[0];
          let message = result.split(":")[1];

          if (status == "OK") {
            this.$refs["my-modal-changePin"].hide();
            this.getAllCards();
            this.$bvModal.msgBoxOk(message, { centered: true, size: "sm" });
          } else {
            this.errorMsg = message;
          }
        });
    },

    transfer() {
      // check input
      if (this.toCardNo == "") {
          this.errorMsg = "Please select card";
          return;
      }

      if (this.transferAmt == "" || Number(this.transferAmt) > this.currentCard.balance || Number(this.transferAmt) == 0) {
          this.errorMsg = "Invalid transfer amount!";
          return;
      }

      if (this.currentCard.pin.length > 0 && this.oldPin == "") {
        this.errorMsg = "Please enter your PIN";
        return;
      }

      barcodeService
        .transferBalance(this.currentCard.cardNo, this.toCardNo, this.transferAmt, this.oldPin)
        .then((resp) => {
          let result = resp.data;
          console.log(result);

          let status = result.split(":")[0];
          let message = result.split(":")[1];

          if (status == "OK") {
            this.$refs["my-modal-transfer"].hide();
            this.getAllCards();
            this.$bvModal.msgBoxOk(message, { centered: true, size: "sm" });
          } else {
            this.errorMsg = message;
          }
        });
    },
  },
  mounted() {
    this.getAllCards();
  },
};
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
.nopadding {
  padding: 0 !important;
  margin: 0 !important;
}
</style>
