import http from "../http-common";
import SockJS from "sockjs-client";
import Stomp from "webstomp-client";
import config from "../myconfig.js";
import store from "../store.js";

class BarCodeService {

  scan(barcode) {
    return http.get("../barcode/scan/" + barcode);
  }

  getCurrentSessionID() {
    return http.get("../barcode/sessionid");
  }

  verifyPin(pin) {
    return http.get("../barcode/verifyPin/" + pin);
  }

  connect() {

    if (this.connected) {
      console.log("already connected");
      return;
    }

    this.socket = new SockJS(config.websocketURL);
    this.stompClient = Stomp.over(this.socket);
    this.stompClient.connect(
      {},
      frame => {

        this.connected = true;
        console.log(frame);

        this.stompClient.subscribe("/topic/barcodeStatus", msg => {
          //console.log("Received message: " + msg.body);
          store.dispatch("barcode/update", JSON.parse(msg.body));
        });

        this.stompClient.send("/app/barcodeStatus", JSON.stringify({}), {});
      },
      error => {
        console.log(error);
        this.connected = false;
      }
    );
  }

  startScan() {
    this.stompClient.send("/app/barcodeStatus", JSON.stringify({started: true}), {});
  }

  proceedToPay() {
    this.stompClient.send("/app/barcodeStatus", JSON.stringify({paymentStarted: true}), {});
  }

  cancelPayment() {
    this.stompClient.send("/app/barcodeStatus", JSON.stringify({cancelPayment: true}), {});
  }

  confirmPayment() {
    this.stompClient.send("/app/barcodeStatus", JSON.stringify({confirmPayment: true}), {});
  }

  startOver() {
    this.stompClient.send("/app/barcodeStatus", JSON.stringify({startOver: true}), {});
  }

  deleteItem(barcodeItem) {
    this.stompClient.send("/app/barcodeStatus", JSON.stringify({deleteFlag: true, barcodeItem: barcodeItem}), {});
  }

  updateItem(barcodeItem) {
    this.stompClient.send("/app/barcodeStatus", JSON.stringify({deleteFlag: false, barcodeItem: barcodeItem}), {});
  }

  disconnect() {
    if (this.stompClient) {
      this.stompClient.disconnect();
    }
    this.connected = false;
  }

  // for gift card management
  getAllGiftCards() {
    return http.get("../barcode/card/all");
  }

  changePIN(cardNo, oldPin, newPin) {
    return http.get("../barcode/card/changePin/" + cardNo + ":" + oldPin + ":" + newPin);
  }

  transferBalance(cardNo1, cardNo2, amount, pin) {
    return http.get("../barcode/card/transfer/" + cardNo1 + ":" + pin + ":" + cardNo2 + ":" + amount);
  } 
}

export default new BarCodeService();
